#timeline {
    background-color: #FDFCFC;
    
    text-align: center;
    padding-bottom: 5rem;
    padding-bottom: 5rem;
}

.timeline-title{
  padding: 30px;
  font-size: 4rem;


}
.timeline {
  position: relative;
  width: 100%;
  max-width: 1140px;
  margin: 0 auto;
  padding: 15px 0;
  
}

.timeline::after {
  content: '';
  position: absolute;
  width: 2px;
  background: rgb(8 51 68);
  top: 0;
  bottom: 0;
  left: 50%;
  margin-left: -1px;
}

.tlentry {
  padding: 15px 30px;
  position: relative;
  background: inherit;
  width: 50%;
}

.tlentry.left {
  left: 0;
}

.tlentry.right {
  left: 50%;
}

.tlentry::after {
  content: '';
  position: absolute;
  width: 16px;
  height: 16px;
  top: calc(50% - 8px);
  right: -8px;
  background: #ffffff;
  border: 2px solid rgb(8 51 68);
  border-radius: 16px;
  z-index: 1;
}

.tlentry.right::after {
  left: -8px;
}

.tlentry::before {
  content: '';
  position: absolute;
  width: 50px;
  height: 2px;
  top: calc(50% - 1px);
  right: 8px;
  background: rgb(8 51 68);
  z-index: 1;
}

.tlentry.right::before {
  left: 8px;
}

.tlentry .date {
  position: absolute;
  display: inline-block;
  top: calc(50% - 8px);
  text-align: center;
  font-size: 14px;
  font-weight: bold;
  color: rgb(8 51 68);
  text-transform: uppercase;
  letter-spacing: 1px;
  z-index: 1;
}

.tlentry.left .date {
  right: -80px;
}
.tlentry.left1 .date {
  right: -9.5rem;
}
.tlentry.left2 .date {
  right: -6.5rem;
}
.tlentry.right .date {
  left: -80px;
}
.tlentry.right1 .date {
  left: -9rem;
}
.tlentry.right2 .date {
  left: -11rem;
}

.tlentry .icon {
  position: absolute;
  display: inline-block;
  width: 25px;
  height: 25px;
  padding: 9px 9px;
  top: calc(50% - 25px);
  /* background: #E9C8C4; */
  background-color: rgb(186 230 253);
  border: 2px solid rgb(8 51 68);
  border-radius: 40px;
  text-align: center;
  font-size: 18px;
  color: rgb(8 51 68);
  z-index: 1;
}

.tlentry.left .icon {
  right: 56px;
}

.tlentry.right .icon {
  left: 56px;
}

.tlentry .tlcontent {
  padding: 30px 90px 30px 30px;
  /* background: #E9C8C4; */
  background-color: rgb(186 230 253); 
  position: relative;
  border-radius: 0 500px 500px 0;
}

.tlentry.right .tlcontent {
  padding: 30px 30px 30px 90px;
  border-radius: 500px 0 0 500px;
}

.tlentry .tlcontent h2 {
  margin: 0 0 10px 0;
  font-size: 18px;
  font-weight: normal;
  color: rgb(8 51 68);
}

.tlentry .tlcontent p {
  margin: 0;
  font-size: 16px;
  line-height: 22px;
  color: #000000;
}

@media (max-width: 767.98px) {
  .timeline::after {
    left: 90px;
  }

  .tlentry {
    width: 100%;
    padding-left: 120px;
    padding-right: 30px;
  }

  .tlentry .date {
    width: 3rem;
    
  }

  .tlentry.right {
    left: 0%;
  }

  .tlentry.left::after, 
  .tlentry.right::after {
    left: 82px;
  }

  .tlentry.left::before,
  .tlentry.right::before {
    left: 100px;
    border-color: transparent rgb(8 51 68) transparent transparent;
  }

  .tlentry.left .date,
  .tlentry.right .date {
    right: auto;
    left: 12px;
  }

  .tlentry.left .icon,
  .tlentry.right .icon {
    right: auto;
    left: 146px;
  }

  .tlentry.left .tlcontent,
  .tlentry.right .tlcontent {
    padding: 30px 30px 30px 90px;
    border-radius: 500px 0 0 500px;
}
}